import React, { useState } from 'react'
import Toolbar from '../toolbar'

/* STYLE DO PRZEBUDOWANIA */
import '../../styles'

const App = () => {

    const [fontSize, setFontSize] =
        useState(100);
    const [contrast, setContrast] =
        useState(false);

    return (
        <React.Fragment>
            <div className={`${contrast ? 'app-content app-content--contrast' : 'app-content'}`} style={{ fontSize: `${fontSize}%` }}>
                <Toolbar fontSize={fontSize} setFontSize={setFontSize} contrast={contrast} setContrast={setContrast} />
            </div>
        </React.Fragment>
    )
};

export default App